 export function Profile(props){
    let v="col-12 col-sm-12 col-md-12",link="https://api.whatsapp.com/send?phone="+props.phone+"&text=Hello%20"+props.name+",%20I%20have%20contacted%20you%20through%20Kayas%20Makerere%20website."
    return (
  
  <div class={v} style={{paddingBottom:"10px"}}><p></p> <div style={{padding:"10px"}}><div className="row"> <div class="col-3 col-sm-4 col-md-5"></div> <div class="col-6 col-sm-4  col-md-2"><img style={{borderRadius:"90px"}} class=" d-block w-100" src={props.img} alt="kayas"/></div> <div class="col-3 col-sm-4 col-md-5"></div><div style={{textAlign:"center",paddingTop:"30px"}}><div style={{fontWeight:"bold",fontSize:"15px", color:"green"}}>{props.name}</div><div style={{textAlign:"center",fontSize:"15px", color:"red"}}>{props.post}</div><div style={{fontWeight:"",textAlign:"center",fontSize:"15px", color:"green"}}>{props.campus}</div>  <div> <a href={link}><button type="button" class="btn btn-sm btn1 btn-success"> <span class="fa fa-whatsapp"> </span> Contact</button></a></div> </div> </div></div> <div style={{paddingLeft:"5px"}}>    <div  style={{color:"grey",textAlign:"center",fontFamily:"Charm",borderBottom:"0.5px solid grey",borderRadius:"20px",padding:"3px",overflow:""}}>{props.msg}</div>    </div></div>
  
    );
  }
