import {Itemsele,ThemeWrap,Alert,Alert2} from '../Home';
import {Guildps} from '../Guildps'
import React, {useEffect,useState} from 'react'
import bd from '../imgs/bd.jpg';
import { CampusAnchors } from '../opinions/OpinionsHome';


 
export function PoliticsHome(){
  

return(
  
<div>


<div style={{fontSize:"30px",color:"red",textAlign:"center",fontFamily:"charm"}}>Politics Home Page</div><p></p>

   <div style={{textAlign:"center",color:"grey",padding:"3px"}}>Let's feel free by adding our messages of love, encouragement and unity through the form just below this photo: <br></br></div>
   <ThemeWrap img={bd}headline="Kawuma Roman" date="Through Kayas Makerere" msg="The Kayas Family wishes to join you, Roman during today's birthday celebrations. May God be with you and be your sole leader in everything. Dear students, feel free to add your messages as well through the form below and also look at what others have shared."/> 
   <div style={{textAlign:"center",padding:"20px"}}> 
   <div style={{color:"green",fontWeight:"bold", fontSize:"20px"}}>POST YOUR MESSAGE</div>
    <form method="post" action="/collection_birthdayMsgs_msg">
<div class="mb-3">


<input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles'   required></input>
<br></br>
<input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter Your WhatsApp Contact'  minLength={10} maxLength={10} required></input>
<br></br>

  <textarea rows="5"  type="text" class="form-control" autoComplete="off" name="msg" placeholder='Type Your Message Here' required></textarea>
 
  
</div>


<button type="submit" class="btn btn-success">Add Your Message</button><p></p>
<a href="https://kayas-mak.herokuapp.com/pages/brocode" ><label style={{color:"green"}}><span class="hovereffect"> Who is Kayas? Get to know</span></label></a>
</form>
    
    
</div>

<div style={{textAlign:"center",fontWeight:"bold",color:"red"}}> birthday messages below:</div>

<Alert/>

<Alert2/> 


</div>
  
 
);
}


export function OpinionPolls(){

  const[cand1Numb,setCand1Numb]=useState('')
  const[cand2Numb,setCand2Numb]=useState('')
  const[cand3Numb,setCand3Numb]=useState('')
  const[cand4Numb,setCand4Numb]=useState('')
  const[cand5Numb,setCand5Numb]=useState('')
  
  useEffect(()=>{
     
  
    fetch('/collections_opinionpolls_cand1').then(res=>res.json()).then(res=>{
     
      
      setCand1Numb(res.length);
        })
        fetch('/collections_opinionpolls_cand2').then(res=>res.json()).then(res=>{
     
      
          setCand2Numb(res.length);
            })
            fetch('/collections_opinionpolls_cand3').then(res=>res.json()).then(res=>{
     
      
              setCand3Numb(res.length);
                })
                fetch('/collections_opinionpolls_cand4').then(res=>res.json()).then(res=>{
     
      
                  setCand4Numb(res.length);
                    })
                    fetch('/collections_opinionpolls_cand5').then(res=>res.json()).then(res=>{
     
      
                      setCand5Numb(res.length);
                        })
    
    },[])




  return(
    <div>
     <div style={{fontSize:"30px",color:"red",textAlign:"center"}}>Mak Opinion Polls</div><p></p>
     <div style={{textAlign:"right",paddingRight:"10px"}}><a style={{color:"grey"}} href="https://kayas-mak.herokuapp.com/pages/campus"><span  class="hovereffect">more stories here <span  style={{height:"19px", width:"19px",color:"grey"}}class="spinner-grow" role="status"></span></span> </a></div>
     
     
      <div style={{padding:"8px",fontSize:"15px",textAlign:"center"}}>
      Alionzi Lawrence a.k.a Dangote: <span style={{color:"red"}}>{cand1Numb} </span> supporters<br></br>
      Namirembe Blessious: <span style={{color:"red"}}>{cand2Numb} </span> supporters<br></br>
      Hussein Ibra: <span style={{color:"red"}}>{cand3Numb} </span> supporters<br></br>
      Muyinda Carlton: <span style={{color:"red"}}>{cand4Numb} </span> supporters<br></br>
      Mugisa Christopher: <span style={{color:"red"}}>{cand5Numb} </span> supporters<br></br>
     </div>
     
     
     <div style={{padding:"8px",fontSize:"15px",color:"grey", textAlign:"center"}}>Feel free to add your choice of support. We will use this as an estimate to see in whose hands Makerere University lies.</div>   
   

     <div style={{textAlign:"center",padding:"20px"}}> 
       <div style={{color:"green",fontWeight:"bold", fontSize:"20px"}}>ADD  YOUR CHOICE</div>
       
        <form method="post" action="/collection_opinionpolls_poll">
    <div class="mb-3">
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter Your WhatsApp Contact'  minLength={10} maxLength={10} required></input>
    <br></br>
    <input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles'   required></input>
    <br></br>
    <div style={{color:"green",textAlign:"left", fontSize:""}}>
Candidate 1: Alionzi Lawrence a.k.a Dangote<br></br>
Candidate 2: Namirembe Blessious<br></br>
Candidate 3: Hussein Ibra<br></br>
Candidate 4: Muyinda Carlton<br></br>
Candidate 5: Mugisa Christopher<br></br>

    </div>
    <input type="text" class="form-control" autoComplete="off" name="candidateNumber" placeholder='Enter candidate number' minLength={1} maxLength={1}  required></input>
    <br></br>
    <input type="text" class="form-control" autoComplete="off" name="pin" placeholder='Enter Your PIN e.g 12345'  minLength={5} maxLength={5} required></input>
    <br></br>
      
     
      
    </div>
        <div class="row">
          <div class="col-6"><button type="submit" class="btn btn-success">Submit your choice</button></div>
          <div class="col-6"><a href="https://kayas-mak.herokuapp.com/pages/register"><button  class="btn btn-success">Register</button></a></div>
        </div>
    <p></p>
    <a href="https://kayas-mak.herokuapp.com/pages/brocode" ><label style={{color:"green"}}><span class="hovereffect"> Who is Kayas? Get to know</span></label></a>
    </form>
        
        
    </div>
    
     
        <CampusAnchors/>
        <Itemsele/>
  
    </div>
  )
}






export default PoliticsHome