export function OpinionsHome(){
  

return(
  
<div>


<div style={{fontSize:"30px",color:"red",textAlign:"center",fontFamily:"charm"}}>Opinions Home Page</div><p></p>

   <div style={{textAlign:"center",color:"grey",padding:"3px"}}>Let's feel free by adding our messages of love, encouragement and unity through the form just below this photo: <br></br></div>
   <ThemeWrap img={bd}headline="Kawuma Roman" date="Through Kayas Makerere" msg="The Kayas Family wishes to join you, Roman during today's birthday celebrations. May God be with you and be your sole leader in everything. Dear students, feel free to add your messages as well through the form below and also look at what others have shared."/> 
   <div style={{textAlign:"center",padding:"20px"}}> 
   <div style={{color:"green",fontWeight:"bold", fontSize:"20px"}}>POST YOUR MESSAGE</div>
    <form method="post" action="/collection_birthdayMsgs_msg">
<div class="mb-3">


<input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles'   required></input>
<br></br>
<input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter Your WhatsApp Contact'  minLength={10} maxLength={10} required></input>
<br></br>

  <textarea rows="5"  type="text" class="form-control" autoComplete="off" name="msg" placeholder='Type Your Message Here' required></textarea>
 
  
</div>


<button type="submit" class="btn btn-success">Add Your Message</button><p></p>
<a href="https://kayas-mak.herokuapp.com/pages/brocode" ><label style={{color:"green"}}><span class="hovereffect"> Who is Kayas? Get to know</span></label></a>
</form>
    
    
</div>

<div style={{textAlign:"center",fontWeight:"bold",color:"red"}}> birthday messages below:</div>

<Alert/>

<Alert2/> 


</div>
  
 
);
}

export function Client(props){
  //<div style={{padding:"8px",fontSize:"15px",color:"grey"}}>Get a customized page like this from Kayas Makerere at only 5,000/= Surprise your beloved one with a page for his or her birthday. It really looks good</div>   
let formActionUrl=`/pages/opinions/${props.id}`
const[opinions,setOpinions]=useState('')
const[opinionsNumb,setOpinionsNumb]=useState('')
let data=""
useEffect(()=>{
   

  fetch(`/opinions/${props.id}`).then(res=>res.json()).then(res=>{
    setOpinionsNumb(res.length)

if(props.showCustomerContact=="on"){
  res.forEach(opinion=>{
   
    data+='<div>'+opinion.name+" "+opinion.contact+"</div><div>"+opinion.msg+"</div><hr></hr>"
  
  })
}

else{

  res.forEach(opinion=>{
   
    data+='<div>'+opinion.name+"</div><div>"+opinion.msg+"</div><hr></hr>"
  
  })
}
  
    
    setOpinions(data);
      })
  
  },[])




  return(
  
    <div>
    
    
    <div style={{fontSize:"25px",color:"red",textAlign:"center"}}>{props.headline1}</div><p></p>
    <div style={{textAlign:"right",paddingRight:"10px"}}><a style={{color:"grey"}} href={props.seeMore}><span  class="hovereffect">more stories here <span  style={{height:"19px", width:"19px",color:"grey"}}class="spinner-grow" role="status"></span></span> </a></div>
    <div style={{padding:"5px",color:"grey"}}><span style={{color:"red", fontSize:"20px"}}>{opinionsNumb}</span> {props.opinionsHeading1}</div>
    
     {props.code}
       <div style={{textAlign:"center",color:"grey",padding:"4px"}}><span style={{color:"red"}}>{opinionsNumb}</span> {props.opinionsHeading2}</div>
       <div style={{textAlign:"center",padding:"20px"}}> 
       <div style={{color:"green",fontWeight:"bold", fontSize:"20px"}}>{props.formHeading}</div>
       
        <form method="post" action={formActionUrl}>
    <div class="mb-3">
    
    <input type="text" class="form-control" autoComplete="off" name="name" placeholder='Your Name e.g. Charles'   required></input>
    <br></br>
    <input type="text" class="form-control" autoComplete="off" name="contact" placeholder='Enter Your WhatsApp Contact'  minLength={10} maxLength={10} required></input>
    <br></br>
    
      <textarea rows="5"  type="text" class="form-control" autoComplete="off" name="msg" placeholder='Type Your Message Here' required></textarea>
     
      
    </div>
        
    <button type="submit" class="btn btn-success">{props.formButtonLabel}</button><p></p>
    <a href="https://kayas-mak.herokuapp.com/pages/brocode" ><label style={{color:"green"}}><span class="hovereffect"> Who is Kayas? Get to know</span></label></a>
    </form>
        
        
    </div>
    
    <div style={{textAlign:"center",fontWeight:"bold",color:"red"}}>{opinionsNumb} {props.opinionsHeading3}</div>
   
<div style={{textAlign:"center",padding:"20px",fontSize:"15px",fontFamily:"charm"}}><div dangerouslySetInnerHTML={{__html:opinions}}/></div>
  

    {props.bottomAnchors}
    </div>
      
     
    );

}


